import React, { Component } from 'react'
import '../testcss/AvadaRoll.css';
export class AvadaRoll extends Component {
    render() {
        return (
            <div className="avadaroll">
                <div className="container">
                    <div className="avadaroll-wrapper row">
                        <div className="f1-roll col-md-4 col-lg-4 col-xl-4">
                            <div className="flip-right">
                                <div className="flip-box-wrapper">
                                    <div className="flip-box-front">
                                        <div className="flip-box-inner">
                                            <div className="flip-icon-front flip-box-circle">
                                                <i className="fas fa-thumbs-up f-icon"></i>
                                            </div>
                                            <div className="flip-front-title">Support System</div>


                                            <span className="flip-front-content">
                                                Our support system is the perfect place to receive more detailed instructions on using your theme.
                                    </span>
                                        </div>
                                    </div>
                                    <div className="flip-box-back">
                                        <div className="flip-back-inner">
                                            <div className="flip-back-title">The GOLDEN RULE</div>
                                            <span className="flip-back-content">We know everyone needs support. Our philosophy is to treat you as we would like to be treated.</span>
                                            <div className="f-button-wrapper">
                                                <a className="f-button">
                                                    <span>
                                                        LEARN MORE
                                            </span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="f2-roll col-md-4 col-lg-4 col-xl-4">
                            <div className="flip-right">
                                <div className="flip-box-wrapper">
                                    <div className="flip-box-front">
                                        <div className="flip-box-inner">
                                            <div className="flip-icon-front flip-box-circle">
                                                    <i className="fas fa-book f-icon"></i>
                                            </div>
                                            <div className="flip-front-title">Extendsive Document</div>


                                            <span className="flip-front-content">
                                                Online and easily accessible, these are detailed instructions on how to use Avada.
                                    </span>
                                        </div>
                                    </div>
                                    <div className="flip-box-back">
                                        <div className="flip-back-inner">
                                            <div className="flip-back-title">Find The Answers</div>
                                            <span className="flip-back-content">This is the place to find just about anything you need to know about using your theme.</span>
                                            <div className="f-button-wrapper">
                                                <a className="f-button">
                                                    <span>
                                                       LEARN MORE
                                            </span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div className="f3-roll col-md-4 col-lg-4 col-xl-4">
                            <div className="flip-right">
                                <div className="flip-box-wrapper">
                                    <div className="flip-box-front">
                                        <div className="flip-box-inner">
                                            <div className="flip-icon-front flip-box-circle">
                                            <i className="fas fa-film f-icon"></i>
                                            </div>
                                            <div className="flip-front-title">HD Video Tutorials</div>


                                            <span className="flip-front-content">
                                                Nothing is easier then learning first hand by watching a nicely composed high-definition video.
                                                </span>
                                        </div>
                                    </div>
                                    <div className="flip-box-back">
                                        <div className="flip-back-inner">
                                            <div className="flip-back-title">Learn By Watching</div>
                                            <span className="flip-back-content">Watch our HD narrated video tutorials to quickly learn how to use various parts of your theme.</span>
                                            <div className="f-button-wrapper">
                                                <a className="f-button">
                                                    <span>
                                                        LEARN MORE
                                            </span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    </div>
                    </div>
                    )
                  }
                }
                
                export default AvadaRoll
