import React, { Component } from 'react'
import '../testcss/ratestar.css';
export class RateStart extends Component {
    render() {
        return (
            <div className="rate-star">
                <div className="container">
                <div className="left-content">
                    <div className="rate-content">
                        <div>
                            <a>
                                <img src=" https://avada.theme-fusion.com/wp-content/uploads/2014/09/support_review.png" />
                            </a>
                        </div>
                    </div>
                </div>
                <div className="right-content">
                    <div className="right-content-wrapper">
                        <div className="img-rate-content">
                            <div>
                                <p>
                                    <a>
                                        <img src="https://avada.theme-fusion.com/wp-content/uploads/2014/09/ipads.jpg" />
                                    </a>
                                </p>
                            </div>

                        </div>
                        
                    </div>
                    <div className="bottom-content">
                        <div className="rate-text">
                            <p>We Are Proudly Serving Over</p>
                        </div>
                        <div className="rate-text1">
                            <span>400,000</span>
                        </div>
                        <div className="rate-star2">
                            <span>
                            Satisfied Customers
                            </span>
                        </div>
                        <div className="rate-btn">
                            <a className="btn-reviews">
                                <span>
                                READ MORE REVIEW!
                                </span>
                            </a>
                        </div>
                    </div>
                    
                </div>
                </div>
              
            </div>
        )
    }
}

export default RateStart
