import React, { Component } from 'react'
import '../testcss/RealSupport.css';
export class RealSupport extends Component {
  render() {
    return (
      <div className="real-support">
            <div className="support-container">
                <div className="support-wrapper">
                    <div className="support-title">
                        <div>
                            <span>
                            Real Support. Real People.
                            </span>
                        </div>
                    </div>
                </div>
                <div className="support-content">
                    <div className="">
                        <span>
                        <p>We know what its like to need support. Our philosophy is to treat you as we would like to be treated. With a hands on support center, online documentation and HD video tutorials, its no wonder that some of our best reviews are about our amazing support.</p>
                        </span>
                            
                       
                    </div>
                </div>
            </div>
      </div>
    )
  }
}

export default RealSupport
