import React, { Component } from 'react'
import '../testcss/NextContent7.css';
export class NextContent7 extends Component {
  render() {
    return (
      <div className="n-content7">
        <div className="container">
            <div className="n-content7-wrapper">
                <div className="n-content7-title">
                    <div className="n-content7-t">
                        <span>Reviews From Around The Globe</span>
                    </div>
                </div>
                <div className="n-content7-details">
                    <div className="n-content7-d">
                        <div className="">
                            <span>People everywhere are loving Avada and we thrive off their feedback. With over 16,500 ratings, Avada maintains an overall 5-Star rating.</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>
    )
  }
}

export default NextContent7
