import React, { Component } from 'react'
import Navmenu from './navmenu';
import Banner from './banner';
import Slidingbar from './slidingbar';
import NavRightMenu from './navRightMenu';
import NextContent from './NextContent';
import NewDescription2 from './NewDescription2';
import NextLayout from './nextLayout';
import DropDownArrow from './DropDownArrow';
import NextContent2 from './NextContent2';
import NextDesciption3 from './NextDesciption3';
import NextContent4 from './NextContent4';
import NextContent5 from './NextContent5';
import DropArrow2 from './DropArrow2';
import NextSelectionTemp from './NextSelectionTemp';
import NextContent6 from './NextContent6';
import RealSupport from './RealSupport';
import RateStart from './RateStart';
import FooterAvada from './FooterAvada';
import AvadaRoll from './AvadaRoll';
import NextContent7 from './NextContent7';
import FooterPage from './FooterPage';
import '../testcss/csspractice.css';
export class csspractice extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
      intevalID: 0,
      isTop: true
    }
  }
  
  scrollStep() {
    if (window.pageYOffset === 0) {
      clearInterval(this.state.intervalId);
    }
    window.scroll(0, window.pageYOffset - 50);
  }

  scrollToTop() {
    let intervalId = setInterval(this.scrollStep.bind(this), 4.44);
    this.setState({ intervalId: intervalId });
  }
  render() {
    const scrollButton = this.state.isTop ? 'hide-scroll' : 'show-scroll';
    return (
      <div>
          <div className="wrapper">
        <Navmenu />
        <Banner />
      
        
        <Slidingbar />
        <NextContent />
        <NewDescription2 />
        <DropDownArrow />
        <NextLayout />
        <NextContent2 />
        <DropDownArrow />
        <NextDesciption3 />
        <NextContent4 />
        <NextContent5/>
        <DropArrow2/>
        <NextSelectionTemp/>
        <DropDownArrow />
        <NextContent6 />
        <RealSupport />
        <RateStart />
        <AvadaRoll />
        <NextContent7/>
        <DropArrow2/>
        <FooterAvada />
        <FooterPage />
        </div>
      <NavRightMenu />
      <div onClick={() => { this.scrollToTop(); }} className={'scroll-btn ' + scrollButton}>
      <i class="fas fa-chevron-up"></i>
        </div>
      </div>
      
    )
  }
  componentDidMount() {
    document.addEventListener('scroll', () => {
      const isTop = window.scrollY < 1;
      if (isTop !== this.state.isTop) {
        this.setState({ isTop })
      }
    });
  }
}

export default csspractice
